import React from 'react'
import './styles.css'
import sizeMe from 'react-sizeme'
import {
  Form,
  FormGroup,
  Label,
  Input,
  Col,
  Button
} from 'reactstrap'
import Map from './GoogleMap'

function Contact(props) {
    const {width} = props.size;

    const contactFlexContainer = {
        'display': 'flex',
        'justifyContent': 'center',
        'height': '750px'
    }
    
    const contactTitle = {
        'display': 'flex',
        'justifyContent': 'center',
        'marginTop': '5%'
    }

    const contactLabel = {
        'fontSize': '150%'
    }

    const contactInput = {
        'height': '50px',
        'borderColor': '#2c3e50',
        'fontSize': '150%'
    }
    
    const contactSubmit = {
        'backgroundColor': '#2ecc71',
        'color': 'white',
        'height': '30%',
        'width': '100%',
        'fontSize': '150%',
        'marginBottom': '5%',
        'marginTop': '5%'
    }
    
    const gotcha = {
        'display': 'none'
    }
    
    const textArea = {
        'height': '200px',
        'borderColor': '#2c3e50'
    }
    
    const contactInputMobile = {
        'height': '10%',
        'borderColor': '#2c3e50',
        'width': '90%',
        'fontSize': '150%'
    }
    const contactSubmitMobile = {
        'backgroundColor': '#2ecc71',
        'color': 'white',
        'height': '30%',
        'width': '90%',
        'fontSize': '100%',
        'marginTop': '1rem'
    }
    const textAreaMobile = {
        'height': '24vh',
        'borderColor': '#2c3e50',
        'width': '90%'
    }
    const form = {
        'padding': '5%'
    }

    const formMobile = {
        'padding': '5%',
        'width': '100%'
    }

    const contact = {
        'display': 'flex',
        'flexDirection': 'column',
        'alignItems': 'center'
    }

    return (
        width >= 1200
        //Desktop Layout
        ? (
            <div>
                {props.navbar}
                <h1 style={contactTitle}>Contact Us</h1>
                <div style={contactFlexContainer}>
                    <Col xs={3} />
                    <Col xs={6}>
                        <Form id='contactform' style={form} action='https://formspree.io/suzanne@medicareandmore.info' method="POST">
                            <FormGroup>
                                <Label style={contactLabel} for='name' sm={1}>Name</Label>
                                <Input style={contactInput} type='text' name='name' id='name' required />
                            </FormGroup>
                            <FormGroup>
                                <Label style={contactLabel} for='phone' sm={1}>Phone</Label>
                                <Input style={contactInput} type='tel' name='phone' id='phone' required />
                            </FormGroup>
                            <FormGroup>
                                <Label style={contactLabel} for='email' sm={1}>Email</Label>
                                <Input style={contactInput} type='email' name='email' id='email' required />
                            </FormGroup>
                            <FormGroup>
                                <Label style={contactLabel} for='message' sm={1}>Message</Label>
                                <Input style={textArea} type='textarea' name='message' id='message' required />
                            </FormGroup>
                            <Input type="hidden" name="_subject" value="New Message for Medicare & More" />
                            <Input type="text" name="_gotcha" style={gotcha} />
                            <Button type='submit' value='Send' style={contactSubmit}>Send</Button>
                        </Form>
                    </Col>
                    <Col xs={3} />
                </div>
                <Map />
            </div>
        )
        //Mobile Layout
        : (
            <div>
                {props.navbar}
                <div style={contact}>
                    <h1 style={contactTitle}>Contact Us</h1>
                    <Form id='contactform' style={formMobile} action='https://formspree.io/suzanne@medicareandmore.info' method="POST">
                        <FormGroup>
                            <Label style={contactLabel} for='name' sm={1}>Name</Label>
                            <Input style={contactInputMobile} type='text' name='name' id='name' required />
                        </FormGroup>
                        <FormGroup>
                            <Label style={contactLabel} for='phone' sm={1}>Phone</Label>
                            <Input style={contactInputMobile} type='tel' name='phone' id='phone' required />
                        </FormGroup>
                        <FormGroup>
                            <Label style={contactLabel} for='email' sm={1}>Email</Label>
                            <Input style={contactInputMobile} type='email' name='email' id='email' required />
                        </FormGroup>
                        <FormGroup>
                            <Label style={contactLabel} for='message' sm={1}>Message</Label>
                            <Input style={textAreaMobile} type='textarea' name='message' id='message' required />
                        </FormGroup>
                        <Input type="hidden" name="_subject" value="New Message for Medicare & More" />
                        <Input type="text" name="_gotcha" style={gotcha} />
                        <Button type='submit' value='Send' style={contactSubmitMobile}>Send</Button>
                    </Form>
                </div>
                <Map />
            </div> 
        )
    )
}

export default sizeMe({ monitorHeight: true })(Contact)